import React, { Suspense } from "react";

const LeaderBoard = React.lazy(() =>
  import("../../analytics-new/content/components/leaderBoard")
);

const LeaderboardDashboard = (props) => {
  const { loader, leaderBoard } = props;

  console.log(leaderBoard,'leaderBoardleaderBoardleaderBoard',props)

  return (leaderBoard && Object.keys(leaderBoard.data).length) || loader ? (
    <div className="col-lg-6">
      <Suspense fallback={""}>
        <div className="home_leaderBoard">
          <LeaderBoard
            isDashboard={1}
            loader={loader}
            selectedWallFeed={[]}
            leaderBoard={leaderBoard}
          />
        </div>
      </Suspense>
    </div>
  ) : null;
};

export default React.memo(LeaderboardDashboard);
